.timesheet {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
    &__title {
        .oswald();
        font-size: 30px;
        text-align: center;
        color: #343434;
        margin-bottom: 10px;
        text-transform: uppercase;
    }
    &__column {
        width: 220px;
        text-align: left;
    }
    &__row {
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
    }
    &__key {
        font-weight: bold;
        font-size: 16px;
    }
    &__value {
        font-size: 16px;
        float: left;
        text-align: right;
    }
}
