.contact-info {
    width: 200px;
    display: inline-block;
    text-align: center;
    padding: 0 35px;
    &__title {
        .generic-title();
    }
    &__inner {
        display: flex;
        justify-content: space-around;
    }
    &__contact-link {
        color: #f0266a;
        text-decoration: none;
        font-weight: bold;
        display: inline-block;
        .hover-animation();
        outline: none;
    }
    &__contact-method {
        font-size: 14px;
        text-align: center;
        width: 40px;
        display: inline-block;
    }
    &__icon {
        fill: #f0266a;
        outline: none;
    }
}
